
import React from "react";

export default class SearchResultItem extends React.PureComponent {
  render() {
    const { audio } = this.props;
    return (
    <div className="searchResultItem" >
      <span
        className="link"
        title={audio.filename}
        onClick={()=>this.props.onRandomAudioLoaded(audio)} >
        <img
          className="cover"
          src="/vinyl.jpg"
          alt="cover"
        />
        { audio.filename.split('/').pop() }
      </span>
      <span className="searchResultActions">
        <span
          className="link"
          onClick={()=>this.props.editAudioFilename(audio)}
        >
          <i className="fas fa-edit"></i>
        </span>
        <span
          className="link"
          onClick={()=>this.props.deleteAudio(audio)} >
          <i className="fas fa-trash"></i>
        </span>
      </span>
    </div>);
  } 
}